import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import settings from '../../util/settings';

export default class PageHeader extends PureComponent {
  constructor(props) {
    super(props);
    this.ASSET_PATH = `${settings.ASSET_PATH}images/pageheader/`;
  }

  render() {
    const { title, description, image } = this.props;
    return (
      <div className="PageHeader" style={{ backgroundImage: `url('${this.ASSET_PATH + (image || 'page-header.jpg')}')` }}>
        <div className="ContentContainer">
          <h1>{ title }</h1>
          <div className="row">
            <p className="col-12 col-md-7 pageheader-description">{ description }</p>
          </div>
        </div>
      </div>
    );
  }
}

PageHeader.propTypes = {
  title: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  image: PropTypes.string
}